<template>
  <div class="home-page">
    <!-- 首页头部 -->
    <!-- headerView -->
    <div class="header-view" :style="whiteHeaderStyle">
      <img class="header-noarea-img" :src="noAreaImgBlack" v-if="isNoArea">
      <div class="header-area-select" style="color: #333333" @click="selectArea" v-if="!isNoArea">
        <span>{{$userInfo.AREA_NAME}}</span>
        <img :src="blackDropdown" alt="">
      </div>
      <div class="header-right">
        <!-- <div class="my-integral-btn" @click="goToMyIntergral" v-show="isShowSign">
          <img :src="signinBlackImg" alt="">
        </div> -->
        <div class="my-notice-btn" @click="toNotice">
          <img :src="systemNoticeBlackImg" alt="">
          <span class="message-point" v-show="noticesNumber > 0"
                :style="{width: noticesNumber > 9 ? (noticesNumber < 100 ? '20px' : '26px') : '15px'}">{{noticesNumber > 99 ? '99+' : noticesNumber}}</span>
        </div>
        <div class="my-notice-btn" @click="toShopCart">
          <img src="@/assets/images/mall/mall_icon_shoppingcart_black.png" alt="">
          <span class="message-point" v-show="skuTypeNum > 0"
                :style="{width: skuTypeNum > 9 ? (skuTypeNum < 100 ? '20px' : '26px') : '15px'}">{{skuTypeNum > 99 ? '99+' : skuTypeNum}}</span>
        </div>
      </div>
    </div>

    <!-- 内容滚动区域 -->
    <div class="scroll-content" ref="scrollContent">
      <van-pull-refresh
          v-model="refreshing"
          @refresh="onRefresh"
      >
        <!-- 首页顶部banner -->
        <div class="header-banner">
          <SwiperBanner v-if="swiperFlag" :swiperImgs="swipeBannerImgs" :areaList="areas"
                        :swiperImgStyle="swiperBannerStyle" :swiperBox="swiperBoxStyle"></SwiperBanner>
        </div>

        <div style="height:300px" v-if="networkError">
          <NetworkError></NetworkError>
        </div>

        <div v-if="!networkError">
          <!--各种应用-->
          <van-grid :column-num="5" :border="false" style="margin: 0 5px">
            <van-grid-item
                v-for="x in apply"
                :key="x.APP_CODE"
                :icon="x.PIC_URL"
                :text="x.APP_NAME"
                :info="x.unpayBillNum>0?x.unpayBillNum:''"
                @click="toAppDetail(x.APP_CODE)"
            />
          </van-grid>

          <!--限时秒杀-->
          <div class="seckillBox" v-show="seckilProductList.length!=0">
            <div class="seckillTitle">
              <div class="seckillTxt">限时秒杀</div>
              <div class="seeMore" @click="goToFlashSaleList">
                <div>查看更多</div>
                <img src="@/assets/images/mall/mall_icon_more.png" alt="">
              </div>
            </div>

            <div class="scrollX" ref="seckillScroll">
              <div class="seckillGoodsItem" ref="seckillGoodsItem" v-for="(activityGoodsItem,index) in seckilProductList"
                   :key="'activityGoodsItem'+index" tapmode  @click="goToFlashSaleGoods(activityGoodsItem)">
                <div class="seckillGoodsImg">
                  <img class="statusLabel"  :src="activityGoodsItem.spuStatus == 0?statusLabel.orange:(activityGoodsItem.spuStatus == 1?statusLabel.red:'')" alt="">
                  <img v-lazy="activityGoodsItem.goodsPic" :key="activityGoodsItem.goodsPic"
                       alt="">
                  <div class="outSellBox" v-show="activityGoodsItem.spuStatus ==3||activityGoodsItem.spuStatus ==4">补货中</div>
                </div>
                <div class="seckillGoodsInfo">
                  <div class="seckillGoodsName">{{activityGoodsItem.spuName}}</div>
                  <div class="seckillGoodsShopInfo">
                    <div class="seckillGoodsShop" style="height: 14px;
              max-width: 90px;
              text-overflow:ellipsis;
              white-space: nowrap;
              overflow: hidden;
              font-family: PingFangSC-Medium;
              font-size: 10px;
              color: #17B7BD;
              letter-spacing: -0.02px;
              padding: 0 4px;
              line-height: 16px;
              border-radius: 14px;
              position: absolute;
              top: 9px;
              box-sizing: border-box;">{{activityGoodsItem.shopType}}<div class="border"></div></div>
                    <div class="priceNum">￥{{activityGoodsItem.spuSkPrice}}</div>
                    <div class="linePriceNum" v-show="activityGoodsItem.originalPrice">￥{{activityGoodsItem.originalPrice}}</div>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <!-- 底部广告位 -->
          <div class="ad-positon-view" v-if="adPositions.length > 0">
            <van-swipe :autoplay="3000" :show-indicators="false">
              <van-swipe-item class="ad-positon-item" v-for="(adPost, index) in adPositions" :key="index">
                <img :src="adPost.PIC_URL"
                     @touchstart="bottomAdTouchStart()"
                     @touchmove="bottomAdTouchMove()"
                     @touchend="bottomAdTouchEnd(adPost)"/>
              </van-swipe-item>
            </van-swipe>
          </div>

          <div class="homeSpace"></div>

          <!-- 品质生活 -->
          <!-- <QualityLife :need-ref="needRef" @ref-ok="refOK"></QualityLife> -->
          <div class="life-header-title" v-show="lifeItems.length > 0">品质生活</div>
          <van-list
            ref="waterFallList"
            v-model="lifeLoading"
            :finished="lifeFinished"
            finished-text=""
            @load="addQualityLifeData"
            style="width:100%;"
            :offset="1000"
            :immediate-check="false"
          >
            <waterfall
              line="v"
              :line-gap="lifeItemW"
              :watch="lifeItems"
              @reflowed="reflowed"
              ref="waterfall"
              :style="waterfallStyle"
              :fixed-height="true"
            >
              <waterfall-slot
                v-for="(item, index) in lifeItems"
                :width="lifeItemW"
                :height="waterfallSlotHeight(item)"
                :order="index"
                :key="item.articleNo + index"
                move-class="item-move"
                :style="waterFallSoltStyle"
              >
                <div class="life-item-box"  @click="clickedLifeItem(item)">
                  <div class="life-item-pic">
                    <!-- <img v-lazy="item.src" alt="" :key="item.src"> -->
                    <img :src="item.src" alt="">
                  </div>
                  <div class="life-title" :style="lifeTitleStyle">{{item.title}}</div>
                </div>
              </waterfall-slot>
            </waterfall>
          </van-list>

          <!-- 页面底部logo -->
          <div class="finishedBox" v-show="lifeFinished && lifeItems.length != 0">
            <img src="@/assets/images/mall/mall_logo.png" alt="">
          </div>
        </div>
      </van-pull-refresh>
    </div>

    <!-- 回到顶部 -->
    <div class="topBtn" v-show="isShowTopBtn" @click="goTopBtn">
        <img src="@/assets/images/mall/top.png" alt="">
    </div>

    <!-- 小区选择弹框 -->
    <van-popup
        v-model="showAreaSelectView"
        @click-overlay="selectAreaCancel"
        position="bottom"
        :style="{ height: '36%' }"
    >
      <!--<div class="areaselect-topbar">
        <div @click="selectAreaCancel">取消</div>
        <span @click="selectAreaOk">确定</span>
      </div>-->
      <van-picker show-toolbar
        :columns="areaNames"
        :default-index="selectAreaIndex"
        confirm-button-text="确定"
        @change="onChange"
        @cancel="selectAreaCancel"
        @confirm="selectAreaOk"/>
    </van-popup>
    <homePageWindow :isShowHomePageWindow="isShowHomeWindow" :areaList="areas"
                    @childByvalue="childByvalue"></homePageWindow>
    <!--大字模式弹窗-->
    <van-dialog v-model="isShowThemeAlert"
      :title="themeAlertTitle"
      :beforeClose="beforeClose"
      confirmButtonText="确定"
      show-cancel-button>
      <div class="themeAlertDialog">
        <div class="message">{{themeAlertMessage}}</div>
        <div class="defaultBox" @click="switchTheme">
          <div class="defaultBtn">标准模式</div>
          <van-switch v-model="normalChecked" active-color="#17B7BD"  inactive-color="#D1D1D1" size="20px" @click.stop="switchTheme"/>
        </div>
        <van-divider :style="{ borderColor: '#e3e3e3', padding: '0 35px',margin:'0' }"/>
        <div class="defaultBox" @click="switchTheme">
          <div class="defaultBtn">大字模式</div>
          <van-switch v-model="largeChecked" active-color="#17B7BD"  inactive-color="#D1D1D1" size="20px" @click.stop="switchTheme"/>
        </div>
      </div>
    </van-dialog>
  </div>
</template>

<script>
  import {homePage} from './homePage'
  export default {
    name: 'homePage',
    mixins:[homePage]
  }
</script>

<style scoped lang="scss">
  $navHeight: 44px;

  /*大字模式弹窗*/
  /deep/.van-dialog__header {
    font-weight: bold !important;
    font-size: 16px !important;
  }
  .themeAlertDialog .message {
    font-size: 14px;
    color: #3f3f3f;
    padding: 5px 35px;
    text-align: center;
  }
  .themeAlertDialog .defaultBox {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 40px;
    background: #fff;
    height: 24px;
  }
  .themeAlertDialog .defaultBtn {
    font-weight: bold;
    font-size: 16px;
    color: #333333;
    letter-spacing: -0.05px;
    line-height: 22px;
  }

  .homeSpace{
    height: 30px;
    width: 100%;
  }
  .home-page {
    box-sizing: border-box;
    position: absolute;
    overflow: hidden;
    width: 100%;
    height: 100%;
    background-color: #F9F9F9;
    display: flex;
    flex-direction: column;
  }

  //首页头部导航栏
  .header-view {
    box-sizing: border-box;
    position: relative;
    width: 100%;
    height: $navHeight;
    padding-left: 15px;
    padding-right: 5px;
    background-color: transparent;
    display: flex;
    justify-content: space-between;
    align-items: center;
    z-index: 66;
  }

  .header-view .header-noarea-img {
    height: 20px;
    width: 70px
  }

  .header-view .header-area-select {
    font-family: PingFangSC-Medium;
    font-size: 20px;
    color: #fff;
    vertical-align: middle;
    text-align: center;

    img {
      margin: 0px 8px;
      width: 13px;
    }
  }

  .header-view .header-right {
    display: flex;
    align-items: center;

    .my-integral-btn,
    .my-notice-btn {
      height: 44px;
      line-height: 44px;
      width: 44px;
      position: relative;

      img {
        position: relative;
        width: 32px;
        top: 50%; /*偏移*/
        transform: translateY(-50%);
      }
    }

    .my-integral-btn:active,
    .my-notice-btn:active {
      opacity: 0.66;;
    }

    .my-notice-btn .message-point {
      position: absolute;
      font-size: 10px;
      color: #ffffff;
      text-align: center;
      /*line-height: 15px;*/
      box-sizing: border-box;
      display:flex;
      justify-content:center;
      align-items:center;
      height: 15px;
      border-radius: 7.5px;
      background-color: red;
      z-index: 2;
      top: 9px;
      left: 16px;
    }
  }

  //滚动区域
  .scroll-content {
    box-sizing: border-box;
    height: 100%;
    width: 100%;
    overflow: auto;
    flex: 1;
  }

  //顶部bannerView
  .header-banner {
    background-color: #F9F9F9;
    width: 100%;
    height: 196px;
  }

  /*.swiperBox {
       padding-top: 50px;
   }*/
  // 小区选择弹框
  .van-popup--bottom {
    overflow: hidden;
  }

  .areaselect-topbar {
    display: flex;
    justify-content: space-between;
    height: 44px;
    font-family: PingFangSC-Regular;
    font-size: 16px;
    text-align: center;
    align-items: center;

    div {
      color: #3A3A3A;
      width: 60px;
      height: 44px;
      line-height: 44px;
    }

    span {
      color: #17B7BD;
      width: 60px;
      height: 44px;
      line-height: 44px;
    }
  }

  // 限时秒杀
  .seckil-cell {
    height: 282px;
  }

  .seckil-cell .seckil-header {
    display: flex;
    position: relative;
    align-items: center;
    height: 52px;

    .header-title {
      font-family: PingFangSC-Medium;
      font-size: 20px;
      color: #333333;
      text-align: left;
      margin-left: 15px;
      margin-right: 15px;
      flex: 1;
    }

    .header-more {
      font-family: PingFangSC-Regular;
      font-size: 14px;
      color: #888888;
      width: 77px;
      margin-right: 15px;
      text-align: center;

      .more-icon {
        display: inline-block;
        background: url(../../assets/images/common/home_icon_more.png) no-repeat;
        background-size: 100%;
        height: 11px;
        width: 6px;
        position: relative;
        top: 1px;
        margin-left: 5px;
      }
    }
  }

  /deep/ .seckil-cell .van-swipe {
    margin: 0px 15px;
    padding: 3px 3px;
  }

  .product-item {
    display: flex;
    height: 218px;
    // width: 100%;
    background-color: #F9F9F9;

    .product-content {
      flex: 1;
      background-color: #fff;
      box-shadow: 0 2px 5px 0 rgba(194, 194, 194, 0.20);
      border-radius: 4px;
      overflow: hidden;

      img {
        width: 120px;
        height: 120px;
      }

      .pr-title {
        margin: 0px 6px;
        // margin-top: 4px;
        font-family: PingFangSC-Medium;
        font-size: 12px;
        color: #333333;
        height: 34px;
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        line-clamp: 2;
        -webkit-box-orient: vertical;
      }

      .pr-sale-price {
        margin-top: 6px;
        margin-left: 6px;
        font-family: PingFangSC-Medium;
        font-size: 14px;
        color: #F43D3D;
        line-height: 14px;
      }

      .pr-origin-price {
        margin-top: 2px;
        margin-left: 7px;
        font-family: PingFangSC-Regular;
        font-size: 10px;
        color: #9A9A9A;
        line-height: 10px;
        text-decoration: line-through
      }

      .pr-source-other {
        position: absolute;
        margin-left: 7px;
        bottom: 11px;
        font-family: PingFangSC-Medium;
        font-size: 10px;
        color: #17B7BD;
        width: 48px;
        height: 14px;
        border: 0.5px solid #17B7BD;
        border-radius: 6px;
        text-align: center;
      }
    }

    .v-line {
      width: 10px;
      height: 100%;
    }
  }


  /*限时秒杀*/
  .seckillBox {
    width: 345px;
    margin: 25px auto 0;

    .seckillTitle {
      display: flex;
      justify-content: space-between;
      margin-bottom: 12px;
      align-items: flex-end;

      .seckillTxt {
        font-family: PingFangSC-Medium;
        font-size: 20px;
        color: #333333;
        line-height: 28px;
        letter-spacing: -0.06px;
        font-weight: 600;
      }

      .seeMore {
        display: flex;
        align-items: center;

        div {
          font-family: PingFangSC-Regular;
          font-size: 14px;
          color: #888888;
          letter-spacing: -0.04px;
        }

        img {
          width: 8px;
        }
      }
    }

    .scrollX {
      width: 345px;
      height: 227px;
      overflow-x: scroll;
      display: flex;
        .seckillGoodsItem {
          width: 120px;
          height: 220px;
          margin-right: 10px;
          background: #FFFFFF;
          box-shadow: 0 2px 5px 0 rgba(194, 194, 194, 0.20);
          border-radius: 4px;

          .seckillGoodsImg {
            width: 120px;
            height: 120px;
            border-radius: 4px 4px 0 0;
            overflow: hidden;
            position: relative;
            img {
              width: 100%;
            }
            .statusLabel{
              width: 30px;
              height: 34px;
              position: absolute;
              top: 0px;
              left: 5px;
            }
            .outSellBox{
              width: 70px;
              height: 70px;
              position: absolute;
              top: 50%;
              left: 50%;
              transform: translate(-50%,-50%);
              border-radius: 50%;
              background: rgba(0, 0, 0, 0.50);
              font-size: 12px;
              color: #FFFFFF;
              letter-spacing: -0.04px;
              line-height: 70px;
              text-align: center;
            }
          }

          .seckillGoodsInfo {
            padding: 4.5px 10px 10px;

            .seckillGoodsName {
              line-height: 17px;
              height: 34px;
              /*overflow: hidden;*/
              display: -webkit-box;overflow: hidden;text-overflow: ellipsis;-webkit-line-clamp: 2;-webkit-box-orient: vertical;
              font-family: PingFangSC-Medium;
              font-size: 12px;
              color: #333333;
              letter-spacing: -0.05px;
            }

            .seckillGoodsShopInfo {
              position: relative;
            }

            .seckillGoodsShop {
              
              .border::after {
                content: '';
                position: absolute;  /* 把父视图设置为relative，方便定位*/
                top: 0;
                left: 0;
                width: 196%;
                height: 24px;
                margin-top: 1px;
                transform: scale(0.5);
                transform-origin: 0 0;
                box-sizing: border-box;
                border-radius: 15px;
                border: 1px solid #17B7BD;
                z-index: 10;
              }
            }

            .seckillGoodsShopAct {
              font-family: PingFangSC-Medium;
              font-size: 10px;
              color: #FFFFFF;
              letter-spacing: -0.02px;
              background: #17B7BD;
            }

            .priceNum {
              font-family: PingFangSC-Medium;
              font-size: 14px;
              color: #F43D3D;
              letter-spacing: -0.04px;
              line-height: 14px;
              font-weight: 600;
              padding-top: 27px;
            }

            .linePriceNum {
              font-family: PingFangSC-Regular;
              font-size: 10px;
              color: #9A9A9A;
              letter-spacing: -0.03px;
              line-height: 10px;
              text-decoration: line-through;
              margin-top: 2px;
              padding-left: 1px;
            }
          }
        }
    }

    .scrollX::-webkit-scrollbar {
      display: none;
    }

    .scrollX::-webkit-scrollbar {
      display: none;
    }
  }

  //底部广告位
  .ad-positon-view {
    width: 100%;
    height: 90px + 26px;

    .van-swipe {
      padding: 30px 0px 0px;

      .van-swipe__track .van-swipe-item {
        text-align: center;

        img {
          width: 345px;
          height: 90px;
          object-fit: cover;
          border-radius: 4px;
        }
      }
    }
  }

  /// 品质生活
  .life-header-title {
    font-family: PingFangSC-Medium;
    font-size: 20px;
    color: #333333;
    text-align: left;
    margin-left: 15px;
    margin-right: 15px;
    margin-bottom: 12px;
    letter-spacing: -0.06px;
    height: 28px;
    line-height: 28px;
  }
  .life-item-box {
    overflow: hidden;
    width: 100%;
    height: 100%;
    background: #ffffff !important;
    box-shadow: 0 2px 5px 0 rgba(194, 194, 194, 0.2) !important;
    border-radius: 4px !important;
    display: flex;
    flex-direction: column;

    .life-item-pic {
      flex: 1;
      overflow: hidden;
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }

    .life-title {
      font-family: PingFangSC-Medium;
      font-size: 14px;
      color: #333333;
      line-height: 20px;
      max-height: 40px;
      line-clamp: 2;
      -webkit-box-orient: vertical;
      letter-spacing: -0.05px;
      overflow: hidden; //超出文本隐藏
      text-overflow: ellipsis; //溢出省略号显示
      display: -webkit-box; //将对象作为弹性伸缩盒子
      -webkit-box-orient: vertical; //设置伸缩盒子的子元素排列方式-从上到下垂直排列
      -webkit-line-clamp: 2;
    }
  }

  .item-move {
    transition: all .5s cubic-bezier(.55,0,.1,1);
    -webkit-transition: all .5s cubic-bezier(.55,0,.1,1);
  }

  /*底部logo*/
  .finishedBox{
    width: 100%;
    text-align: center;
    margin-bottom: 20px;
    img{
      width: 75px;
      height: 21px;
    }
  }
  // 回到顶部按钮样式
  .topBtn{
      width: 50px;
      height: 50px;
      position: fixed;
      bottom: 150px;
      right: 20px;
      z-index: 10;
      img{
          width: 100%;
      }
  }
  /*小区选择弹窗*/
  /deep/.van-picker__cancel {
    color: #3a3a3a;
  }
  /deep/.van-picker__confirm {
    color: #17B7BD;
  }
  /deep/.van-hairline--top-bottom::after, .van-hairline-unset--top-bottom::after {
    border-width: 0 !important;
  }
  /deep/ .van-grid-item__text{
    color: #4a4a4a;
  }
</style>
